<template>
  <div>
    <el-button-group class="post-list-btns box-content">
      <el-button
        type="primary"
        icon="el-icon-circle-plus-outline"
        @click="$router.push('/post/new')">
        
      </el-button>
      <el-button
        :disabled="disabled.edit"
        type="primary"
        icon="el-icon-edit"
        @click="editItem">
        
      </el-button>
      <el-button
        :disabled="!disabled.changeStatus"
        type="primary"
        @click="changeStatus">
        <i class="iconfont icon-fabu"></i>
        {{statusMap[disabled.changeStatus]||'/'}}
      </el-button>
      <el-button
        :disabled="disabled.del"
        type="primary"
        icon="el-icon-delete"
        @click="delItem">
        
      </el-button>
      <el-button
        type="primary"
        icon="el-icon-check"
        @click="tableOpt.multiMode = !tableOpt.multiMode">
        {{tableOpt.multiMode?'':''}}
      </el-button>
    </el-button-group>

    <el-select
      class="box-content"
      v-model="filter.status"
      clearable
      placeholder="">
      <el-option label="" value="1" />
      <el-option label="" value="2" />
    </el-select>

    <el-select
      class="box-content"
      v-model="filter.tag"
      clearable
      placeholder="">
      <el-option
        v-for="tag in tagList"
        :key="tag._id"
        :label="tag.name"
        :value="tag._id" />
    </el-select>

    <el-cascader
      class="box-content"
      v-model="filter.category"
      :options="categoryList"
      :props="{value:'_id','label':'name',children:'children'}"
      clearable
      placeholder="" />

    <el-input
      class="box-content filter-input"
      :value="filter.title"
      prefix-icon="el-icon-search"
      placeholder=""
      @change="val => filter.title = val">
      <i
        class="el-input__icon el-icon-circle-close"
        slot="suffix"
        v-show="filter.title !== ''"
        @click="filter.title = ''" />
    </el-input>
    
    <el-table
      class="box-content"
      :data="list"
      :height="500"
      :row-class-name="tableRowClass"
      :show-overflow-tooltip="true"
      size="small"
      style="width: 100%"
      @row-click="tableRowClick"
      @sort-change="tabelSortChange">
      <el-table-column
        prop="title"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        label=""
        width="80">
        <template slot-scope="scoped">
          {{{'1': '', '2': ''}[scoped.row.publishStatus]}}
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="180">
        <template slot-scope="scoped">
          <el-tag
            v-for="item in scoped.row.tags.filter(Boolean)"
            :key="item.id"
            color="rgba(210, 250, 255, 0.5)"
            size="small">{{item.name}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="180">
        <template slot-scope="scoped">
          {{scoped.row.category.map(item => item.name).join('/')}}
        </template>
      </el-table-column>
      <el-table-column
        prop="date"
        sortable="custom"
        label="">
      </el-table-column>
    </el-table>

    <el-pagination
      class="box-content"
      :current-page="curPage"
      :page-sizes="[20, 40, 80]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @current-change="currentPageChange"
      @size-change="sizeChange">
    </el-pagination>

  </div>
</template>

<script>
import elTab from './../assets/mixins/elTab.js';
import elPage from './../assets/mixins/elPage.js';
import elTable from './../assets/mixins/elTable.js';
import util from './../assets/js/util.js';

export default {
  name: 'PostList',
  mixins: [elTab, elPage, elTable],
  data () {
    return {
      list: [],
      tagList: [],
      categoryList: [],

      filter: {
        tag: '',
        category: [],
        dateOrder: '',
        title: '',
        status: ''
      },
      statusMap: {
        '1': '',
        '2': ''
      }
    };
  },
  computed: {
    disabled() {
      const result = {
        edit: false,
        del: false,
        changeStatus: ''
      };
      const curSelect = this.curSelect;
      switch (curSelect.length) {
        case 0:
          result.edit = true;
          result.del = true;
          break;
        case 1:
          result.changeStatus = curSelect[0].publishStatus;
          break;
        default:
          result.edit = true;
          let firstStatus = curSelect[0].publishStatus;
          curSelect.some(element => {
            if (element.publishStatus !== firstStatus) {
              firstStatus = '';
              return true;
            }
          });
          result.changeStatus = firstStatus;
          break;
      }
      return result;
    }
  },
  watch: {
    filter: {
      deep: true,
      handler() {
        this.getList();
      }
    }
  },
  created () {
    this.addTab('', '/post');
    this.getList();
    this.getTags();
    this.getCategories();
  },
  methods: {
    async getList() {
      const { tag, category, dateOrder, title, status } = this.filter;
      const params = {
        page: this.curPage,
        size: this.pageSize
      }
      if (category.length > 0) {
        params.category = category[category.length - 1]
      }
      tag && (params.tag = tag);
      dateOrder && (params.dateOrder = dateOrder);
      title && (params.title = title);
      status && (params.publishStatus = status);

      try {
        const { sources, total } = await this.api.getPost(params);
        this.list = sources;
        this.total = total;
      } catch (error) {
        console.log(error);
      }
    },
    async getTags() {
      try {
        const { sources } = await this.api.getTags();
        this.tagList = sources;
      } catch (error) {
        console.error(error);
      }
    },
    async getCategories() {
      try {
        const { sources } = await this.api.getCategory();
        sources.push({
          _id: '0',
          pId: '-1',
          name: ''
        });
        this.categoryList = util.list2tree(sources, '0', {
          id: '_id',
          pId: 'pId',
          label: 'name'
        })[0].children || [];
        this.categoryList.push({
          _id: 'nocategory',
          pId: '0',
          name: ''
        });
      } catch (error) {
        console.error(error);
      }
    },
    delItem() {
      const ids = [];
      const names = [];
      this.curSelect.forEach(element => {
        ids.push(element._id);
        names.push('' + element.title + '');
      });
      this.$msgbox({
        title: '',
        message: '' + names.join('') + '',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose: async (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true;
            try {
              await this.api.delPost(ids.join(','));
              this.getList();
              this.clearRowSelect();
              this.removePostTab(ids);
              instance.confirmButtonLoading = false;
              done();
            } catch (error) {
              console.log(error);
              instance.confirmButtonLoading = false;
              this.$message({
                message: '!',
                type: 'error'
              });
            }
          } else {
            instance.confirmButtonLoading = false;
            done();
          }
          
        }
      }).catch(() => {});
    },
    removePostTab(ids) {
      const routes = ids.map(id => '/post/edit/' + id);
      this.$store.commit('removeTabsByRoute', routes);
    },
    editItem() {
      const id = this.curSelect[0]._id;
      this.$router.push('/post/edit/' + id);
    },
    tabelSortChange({ order }) {
      console.log(order);
      if (order) {
        this.filter.dateOrder = {
          ascending: '1',
          descending: '-1'
        }[order];
      } else {
        this.filter.dateOrder = ''
      }
    },
    changeStatus() {
      const curStatus = this.disabled.changeStatus;
      const publishStatus = curStatus === '1' ? '2' : '1';
      const operateName = this.statusMap[curStatus];
      const names = [];
      const ids = [];
      this.curSelect.forEach(element => {
        ids.push(element._id);
        names.push(element.title);
      });
      const params = {
        ids: ids.join(','),
        publishStatus
      };

      this.$msgbox({
        title: '',
        message: '' + operateName + '' + names.join('') + '',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose: async (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true;
            try {
              await this.api.updatePostStatus(params);
              this.curSelect.forEach(element => {
                element.publishStatus = publishStatus;
              });
              instance.confirmButtonLoading = false;
              done();
            } catch (error) {
              console.log(error);
              instance.confirmButtonLoading = false;
            }
          } else {
            instance.confirmButtonLoading = false;
            done();
          }
        }
      }).catch(() => {});
    }
  }
};
</script>

<style scoped>
.post-list-btns,.el-select,.el-cascader,.filter-input{
  margin-bottom: 15px;
}
.el-select,.el-cascader,.filter-input{
  vertical-align: middle;
  width: 160px;
}
.el-pagination{
  text-align: right;
  margin-top: 5px;
  padding: 5px 10px;
  background-color: #fff;
}
.icon-fabu{
  height: 12px;
  font-size: 12px;
  line-height: 1;
  vertical-align: baseline;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
}
</style>



