import { Message, MessageBox } from 'element-ui';
import axios from 'axios';
import Cookies from 'js-cookie';
import router from './../../router/index';
import ReqMap from './ReqMap';

const { CancelToken } = axios;
const reqMap = new ReqMap();
let loginMsgShow = false;
let reqId = 0;

const axioswp = axios.create({
  timeout: 10000
});

axioswp.interceptors.request.use(function (config) {
  const { url, cancelSame, noAuth } = config;
  const source = CancelToken.source();
  const curReqId = reqId++;
  source.id = curReqId;
  config.id = curReqId;

  // 
  config.cancelToken = source.token;

  // map,url(config)
  reqMap.push({ url, source }, typeof cancelSame !== 'boolean' ? true : cancelSame);

  // ,`Authorization`
  if (!noAuth) {
    config.headers.Authorization = Cookies.get('blog-admin-token');
  }

  return config;
}, function (error) {
  // Do something with request error
  const { url, id } = error.config;
  
  console.log(error);
  // ,map
  reqMap.remove(url, id);
  return Promise.reject(error);
});

axioswp.interceptors.response.use(function (response) {
  // Do something with response data
  const { data, config: { url, id } } = response;

  // ,map
  reqMap.remove(url, id);

  // 403
  if (data.code && data.code === 403) {
    if (!loginMsgShow) {
      loginMsgShow = true;
      MessageBox.alert(',!', '', {
        confirmButtonText: '',
        callback: action => {
          router.push('/login');
          loginMsgShow = false;
        }
      });
    }
    return Promise.reject(data);
  } else if (data.code && data.code === 401) {
    MessageBox.alert('!', '', {
      confirmButtonText: '',
      callback: action => {
      }
    });
    return Promise.reject(data);
  } else if (data.code && data.code !== 200) {
    return Promise.reject(data);
  }
  return data;
}, function (error) {
  
  if (axios.isCancel(error)) {
    return Promise.reject(new Error('cancel request'));
  }
  
  const { url, id } = error.config;
  // ,map
  reqMap.remove(url, id);
  
  // Do something with response error
  if (error.response) {
    // The request was made and the server responded with a status code
    // that falls out of the range of 2xx
    if (error.response.state === 403) {
      MessageBox.alert(',!', '', {
        confirmButtonText: '',
        callback: action => {
          router.push('/login');
        }
      });
    } else {
      Message({
        message: '!',
        type: 'error'
      });
    }
  } else if (error.request) {
    // The request was made but no response was received
    // `error.request` is an instance of XMLHttpRequest in the browser
    MessageBox.alert(error.code === 'ECONNABORTED' ? ',!' : error.message, '', {
      confirmButtonText: '',
      callback: action => {
        if (!window.location.href.includes('login')) {
          // router.push('/error')
        }
      }
    });
  } else {
    // Something happened in setting up the request that triggered an Error
    console.log('Error', error.message);
  }
  return Promise.reject(error);
});

export default {
  axioswp,
  removeAll() {
    setTimeout(() => {
      reqMap.removeAll();
    }, 0);
  }
};



