import qs from 'qs';
import { Message } from 'element-ui';
import APIS from './serverApi';
import axiosUtil from './axioswp';
import util from './../js/util';

const { assert } = util;
const { removeAll, axioswp: axios } = axiosUtil;

const getCategory = function () {
  return axios.get(APIS.category).catch(err => {
    console.log(err);
    Message({
      message: '!',
      type: 'error'
    });
  });
};

const newCategory = function (arg) {
  assert(arg, [{
    field: 'name',
    required: true,
    type: 'string'
  }]);
  const params = qs.stringify(arg);
  return axios.post(APIS.category, params).then(res => {
    Message({
      message: '!',
      type: 'success'
    });
    return res;
  }).catch(err => {
    console.error(err);
    let errMsg = '!';
    if (err.msg && err.msg.includes('duplicate key error')) {
      errMsg = '!';
    }
    Message({
      message: errMsg,
      type: 'error'
    });
  });
};

const editCategory = function (arg) {
  assert(arg, [{
    field: 'name',
    required: true,
    type: 'string'
  }, {
    field: '_id',
    required: true,
    type: 'string'
  }]);
  const params = qs.stringify(arg);
  return axios.put(APIS.category, params).then(res => {
    Message({
      message: '!',
      type: 'success'
    });
    return res;
  }).catch(err => {
    console.error(err);
    let errMsg = '!';
    if (err.msg && err.msg.includes('duplicate key error')) {
      errMsg = '!';
    }
    Message({
      message: errMsg,
      type: 'error'
    });
  });
};

const delCategory = function (arg) {
  assert(arg, [{
    field: 'ids',
    required: true,
    type: 'string'
  }]);
  return axios.delete(APIS.category, { params: arg }).then(res => {
    Message({
      message: '!',
      type: 'success'
    });
    return res;
  }).catch(err => {
    console.error(err);
    Message({
      message: '!',
      type: 'error'
    });
  });
};

export {
  getCategory,
  newCategory,
  editCategory,
  delCategory
};



