<template>
  <div class="header-wp">
    <el-popover
      ref="popover"
      placement="bottom"
      width="100"
      trigger="hover">
      <ul>
        <li class="header-user-operate" @click="checkMyself"><i class="iconfont icon-ziliao"></i></li>
        <li class="header-user-operate" @click="logout"><i class="iconfont icon-dengchu"></i></li>
      </ul>
    </el-popover>

    <div class="header-avatar-wp">
      <span class="text-primary-color header-username">Hi{{user.username}}</span>
      <div class="header-avatar-shade" :style="avatarBg" v-popover:popover>
        <img :src="user.userPic" v-show="user.userPic" alt="avatar" class="header-avatar">
      </div>
    </div>
  </div>
</template>

<script>
import config from './../assets/js/config.js';
export default {
  name: 'TheHeader',
  data () {
    return {
      user: this.$store.state.user
    }
  },
  computed: {
    avatarBg() {
      const backgroundImage = 'url(' + config.staticDir + 'imgs/avatar-' + (this.user.sex === '2' ? 'girl' : 'boy') + '.png)';
      return {
        backgroundImage
      }
    }
  },
  created() {
    if (!this.user._id) {
      this.getCurUser();
    }
  },
  methods: {
    async getCurUser() {
      const params = {
        _id: this.Cookies.get('_id')
      }
      try {
        const { sources } = await this.api.getUsers(params);
        if (sources && sources.length > 0) {
          this.$store.commit('updateUser', sources[0]);
        }
      } catch (error) {
        
      }
    },
    checkMyself() {
      const userId = this.$store.state.user._id;
      if (userId) {
        this.$router.push('/user/edit/' + userId);
      } else {
        this.$message({
          message: '',
          type: 'info'
        });
      }
    },
    logout() {
      this.$store.commit('updateUser', {
        _id: '',
        userPic: '',
        username: ''
      });
      this.$router.push('/login');
    }
  }
}
</script>

<style scoped>
.header-wp{
  width: 100%;
  height: 100%;
}
.header-avatar-wp{
  float: right;
  height: 100%;
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header-avatar-shade{
  height: 50px;
  width: 50px;
  border-radius: 50%;
  overflow: hidden;
  background-size: contain;
}
.header-avatar{
  width: 100%;
  height: 100%;
}
.header-username{
  font-size: 14px;
  margin-right: 10px;
}
.header-user-operate{
  cursor: pointer;
  padding: 4px 8px;
}
.header-user-operate:hover{
  background-color: #ecf5ff;
}
.el-popover{
  padding: 6px 0;
}
</style>



